export default {

	data() {

		return {
			phone: '400-0000-000',
		}
	},

	methods: {
		goExitApp(){
			uni.showModal({
				title:"信息提示",
				content:"是否确认退出程序",
				success:function(res){
					if (res.confirm){
						uni.removeStorageSync("token");
						uni.reLaunch({
							url:"/pages/sign/sign"
						})
						
					}
				}
			})	
		},
		getCall() {
			uni.makePhoneCall({
				phoneNumber: this.phone
			});
		},
		/* 跳转修改密码 */
		pwd() {
			uni.navigateTo({
				url: "/pages/my/pwd"
			})


		},
		baCk(){
			uni.switchTab({
				url:'/pages/my/my'
			})
		},
		praise() {
			uni.navigateTo({
				url: "/pages/my/praise"
			})


		},
		/* 性别选择 */
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;
		},
		// showMulLinkageThreePicker() {
		// 	this.$refs.mpvueCityPicker.show();
		// },

		// onCancel(e) {
		// 	console.log(e);
		// },

	}

}
